@media (min-width: 1441px) {
  .slider {
    &__item {
      background-size: contain;
    }
    .swiper-button-prev {
      left: 2.5%;
    }
    .swiper-button-next {
      right: 2.5%;
    }
  }
}
@media (min-width: 1650px) {
  .slider {
    .swiper-button-prev {
      left: 5%;
    }
    .swiper-button-next {
      right: 5%;
    }
  }
}
@media (min-width: 1920px) {
  .slider {
    .swiper-button-prev {
      left: 7%;
    }
    .swiper-button-next {
      right: 7%;
    }
  }
}
@media (max-width: 1440px) {
  .slider {
    &__item {
      background-size: 55%;
    }
  }
}
@media (max-width: 1400px) {
  .slider {
    &__item {
      padding-left: 110px;
    }
  }
}
@media (max-width: 1200px) {
  .slider {
    &__item {
      background-position: 110% bottom;
      padding-top: 60px;
    }
    .swiper-button-prev,
    .swiper-button-next {
      --size: 70px;
      width: var(--size);
      height: var(--size);
    }

  }
}
@media (max-width: 1000px) {
  .slider {
    &__item {
      padding-left: 70px;
    }
  }
}
@media (max-width: 840px) {
  .slider {
    &__item {
      background: url(../img/content/img1.png) no-repeat, rgba(245, 219, 70, 0.3);
      background-position: 170% bottom;
      background-size: 70%;
      background-blend-mode: overlay;
    }
  }
}
@media (max-width: 650px) {
  .slider {
    &__item {
      background: url(../img/content/img1.png) no-repeat, rgba(245, 219, 70, 0.3);
      background-position: 170% bottom;
      background-size: 70%;
      background-blend-mode: overlay;
    }
    &__title {
      font-size: 6rem;
    }
  }
}
@media (max-width: 560px) {
  .slider {
    &__item {
      background-image: none;
      padding-right: 70px;
    }
    &__content {
      flex-basis: auto;
    }
    &__title {
      font-size: 4rem;
    }
    &__text {
      font-size: 1.5rem;
    }
  }
}
@media (max-width: 450px) {
  .slider {
    &__item {
      background-image: none;
      padding-right: 50px;
      padding-left: 50px;
    }
    &__title {
      font-size: 3rem;
    }
    &__text {
      font-size: 1.5rem;
    }
    .swiper-button-prev,
    .swiper-button-next {
      --size: 50px;
      width: var(--size);
      height: var(--size);
    }
    &__btn {
      & > a {
        padding: 15px 30px 12px;
      }
    }
  }
}


@media (min-width: 1280px) {
  .estate {
    &__img {
      background: url(../img/content/img4.png) no-repeat left center / contain;
    }
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .features-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &__item {
      flex-basis: 30%;
      flex-grow: 1;
      margin-bottom: 50px;
    }
  }
  .calculate {
    &__content {
      padding-left: 50px;
    }
  }
  .pledge-list {
    &__title {
      min-height: 60px;
    }
  }
  .footer {
    &__contact {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__social-btn {
      margin-left: 30px;
    }
  }
  .footer-form {
    flex-direction: column;
    align-items: center;
    padding-left: 30px;
    padding-right: 30px;
    &__tel {
      & > input {
        margin-bottom: 30px;
      }
    }
  }
}

@media (max-width: 991px) {
  .header {
    border-bottom: 5px solid #888888;
    &__burger {
      display: block;
    }
    &__contacts {
      display: none;
    }
    &__menu {
      display: none;
    }
  }
  .estate {
    background-image: url(../img/content/img4_opacity.png);
    background-position: right center;
    background-size: auto;
    background-repeat: no-repeat;
    &__content {
      flex-basis: auto;
      padding-right: 200px;
    }
    &__img {
      display: none;
      background-image: none;
    }
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .features-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &__item {
      flex-basis: 45%;
      flex-grow: 1;
      margin-bottom: 50px;
    }
  }
  .calculate {
    &__inner {
      flex-direction: column;
      align-items: center;
    }
    &__calculator {
      order: 2;
    }
    &__content {
      margin-bottom: 50px;

    }
  }
  .pledge-list {
    flex-wrap: wrap;
    justify-content: space-around;
    &__item {
      flex-basis: 35%;
      margin-bottom: 50px;
    }
    &__title {
      min-height: 60px;
    }
  }
  .footer {
    &__inner {
      padding: 40px 0;
      flex-direction: column;
    }
    &__logo {
      margin-bottom: 40px;
    }
  }
  .footer-form {
    flex-direction: column;
    align-items: center;
    &__tel {
      & > input {
        margin-bottom: 30px;
        padding-right: 0;
      }
    }
  }
}

@media (max-width: 767px) {
  .features-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &__item {
      flex-basis: 45%;
      flex-grow: 1;
      margin-bottom: 50px;
    }
  }
  .about {
    &__row {
      flex-direction: column;
    }
    &__img {
      margin-bottom: 50px;
      & > img {
        width: 40%;
      }
    }
  }
  .features {
    &__row {
      padding: 0 0;
    }
  }
  .conditions {
    background-size: 60%;
    background-position: 100% bottom;
    
  }
  .calculate {
    &__inner {
      flex-direction: column;
      align-items: center;
    }
    &__calculator {
      order: 2;
      flex-basis: auto;
    }
    &__content {
      margin-bottom: 50px;

    }
  }
  .pledge-list {
    flex-wrap: wrap;
    justify-content: space-around;
    &__item {
      flex-basis: 35%;
      margin-bottom: 50px;
    }
    &__title {
      min-height: 60px;
    }
  }
  .footer {
    &__inner {
      padding: 40px 0;
      flex-direction: column;
    }
    &__logo {
      margin-bottom: 40px;
    }
  }
  .footer-form {
    flex-direction: column;
    align-items: center;
    &__tel {
      & > input {
        margin-bottom: 30px;
        padding-right: 0;
      }
    }
  }
}

@media (max-width: 649px) {
  .conditions {
    background-image: none;
    &__content {
      max-width: none;
    }
  }
  .calculate {
    padding-left: 0;
    padding-right: 0;
    &__question {
      flex-direction: column;
    }
    &__grey-text {
      margin-bottom: 25px;
    }
    &__content {
      flex-basis: auto;
    }
    &__text {
      --p: 40px;
      padding-left: var(--p);
      padding-right: var(--p);
    }
  }
  .autopawnshop {
    background: url(../img/content/img3.jpg) no-repeat center bottom / 80%;
    &__inner {
      flex-direction: column;
    }
    &__content {
      margin-bottom: 30px;
      padding-right: 0;
    }
    &__request {
      order: 2;
      background-image: none;
      padding-bottom: 35%;
      flex-basis: auto;
      display: flex;
      justify-content: center;
    }
    &__btn {
      margin: auto;
    }
  }
  .pledge-list {
    &__item {
      flex-basis: 45%;
    }
  }
  .estate {
    background-image: none;
    &__content {
      padding-right: 50px;
    }
    &__btn {
      text-align: center;
    }
  }
}

@media (max-width: 480px) {
  .calculator {
    max-width: 300px;
    &__tab > label {
      font-size: 1.3rem;
      padding: 6px 15px;
    }
  }
  .about {
    &__img {
      & > img {
        width: 60%;
      }
    }
  }
  .features {
    &__row {
      padding: 0 60px;
    }
  }
  .pledge-list {
    &__item {
      flex-basis: 70%;
    }
  }
  .estate {
    &__content {
      padding-left: 0;
      padding-right: 0;
    }
  }
  .footer {
    &__social {
      flex-direction: column;
    }
    &__social-title {
      margin-bottom: 20px;
    }
    &__social-btn {
      --m: 10px;
      margin-left: var(--m);
      margin-right: var(--m);
    }
  }
}

@media (max-width: 380px) {
  .header {
    &__logo {
      & > img {
        width: 80%;
      }
    }
  }
  .calculate {
    &__text {
        --p: 10px;
      padding-left: var(--p);
      padding-right: var(--p);
    }
  }
}